
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.drifter-box-wrapper {
  display: flex;
  overflow: hidden;
  padding: .3rem;
  border: .125rem solid var(--accent-color);
  border-radius: .6rem;
}

.drifter-box-wrapper > :only-child {
  flex-grow: 1;
}

.drifter-analyser {
  /* public properties */
  --analyser-color: var(--accent-color-complement);
  /* protected properties */
  --analyser-height: 2.4rem;
  --analyser-line-height: .125rem;
  display: flex;
  align-items: flex-end;
  justify-content: stretch;
  height: var(--analyser-height);
  padding: 0;
  border-radius: calc(var(--analyser-height) * .3);
}

.drifter-analyser-bar {
  --analyser-bar-amplitude: 0;
  flex-grow: 1;
  height: calc(
    (var(--analyser-height) * .5) +
    /* for a 'waveform' type analyser, the amplitude value will be in the range [-1, 1] */
    (var(--analyser-bar-amplitude) * 50%) +
    (var(--analyser-line-height) * .5)
  );
  border-top: var(--analyser-line-height) solid var(--analyser-color);
}


.drifter-analysers.drifter-analysers-flow {
  display: flex;
}

.drifter-analysers.drifter-analysers-flow > * {
  flex-grow: 1;
}

.drifter-analysers.drifter-analysers-stack {
  display: grid;
}

.drifter-analysers.drifter-analysers-stack > * {
  /* make all grid children overlap on a single cell */
  grid-row-start: 1;
  grid-row-start: 1;
  grid-column-start: 1;
  grid-column-end: 1;
}


.drifter-input {
  --input-height: 1.5rem;
  height: var(--input-height);
  padding: 0;
  margin: 0;
  outline: none;
  border: 0 none;
  border-radius: calc(var(--input-height) * .35);
  font: bold 1rem trench, sans-serif;
  color: var(--accent-color);
  background-color: transparent;
}

.drifter-input:hover,
.drifter-input:focus {
  color: var(--accent-color-complement);
}

.drifter-checkbox {
  --checkbox-size: 1rem;
  -webkit-appearance: none;
          appearance: none;
  width: var(--checkbox-size);
  height: var(--checkbox-size);
  border-radius: calc(var(--checkbox-size) * .35);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.drifter-checkbox:checked:active,
.drifter-checkbox:hover:not(:active) {
  background-color: var(--accent-color-complement);
}

.drifter-checkbox:active,
.drifter-checkbox:checked:not(:active) {
  background-color: var(--accent-color);
}


.drifter-layout {
  --panel-pad: 2rem;
  --aside-width: min(max(30vmax, 12rem), 100vmin);
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: trench, sans-serif;
  color: var(--foreground-color);
  background-color: var(--background-color);
}

.drifter-layout-main {
  overflow: hidden;
  flex-grow: 1;
}

.drifter-layout-sidebar {
  flex-shrink: 0;
  position: relative;
  width: 0;
  padding: var(--panel-pad);
  padding-right: 0;
  background-color: var(--background-color-complement);
  transition: all 444ms ease-in 44ms;
}

.drifter-layout-sidebar:before {
  content: attr(data-heading);
  position: absolute;
  top: .75rem;
  left: .75rem;
}

.drifter-layout-sidebar:hover,
.drifter-layout .drifter-layout-sidebar-pin:checked + .drifter-layout-sidebar {
  width: var(--aside-width);
}

.drifter-layout .drifter-layout-sidebar-pin {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--panel-pad);
  height: var(--panel-pad);
  background-color: transparent !important;
}

.drifter-layout .drifter-layout-sidebar-pin:before {
  content: '\01f588';
  color: var(--accent-color);
}

.drifter-layout .drifter-layout-sidebar-pin:checked:before {
  color: var(--accent-color-complement);
}

/* avoid the sidebar jumping back when it's open and the user hovers over the pin button */
.drifter-layout .drifter-layout-sidebar-pin:not(:checked):hover + .drifter-layout-sidebar {
  transition-delay: 1000000s;
}
.drifter-layout .drifter-layout-sidebar-pin:checked:hover + .drifter-layout-sidebar {
  transition-delay: 0s;
}


.drifter-range-input {
  --range-input-thumb-size: 1rem;
  --range-input-track-height: .5rem;
  -webkit-appearance: none;
  background: transparent;
}

.drifter-range-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: var(--range-input-thumb-size);
  height: var(--range-input-thumb-size);
  -webkit-margin-before: calc((var(--range-input-track-height) * .5) * -1);
          margin-block-start: calc((var(--range-input-track-height) * .5) * -1);
  border-radius: 50%;
  background-color: var(--accent-color);
  cursor: pointer;
}

.drifter-range-input::-moz-range-thumb {
  width: var(--range-input-thumb-size);
  height: var(--range-input-thumb-size);
  margin-block-start: calc((var(--range-input-track-height) * .5) * -1);
  border-radius: 50%;
  background-color: var(--accent-color);
  cursor: pointer;
}

.drifter-range-input::-ms-thumb {
  width: var(--range-input-thumb-size);
  height: var(--range-input-thumb-size);
  margin-block-start: calc((var(--range-input-track-height) * .5) * -1);
  border-radius: 50%;
  background-color: var(--accent-color);
  cursor: pointer;
}

.drifter-range-input::-webkit-slider-runnable-track {
  height: var(--range-input-track-height);
  border-radius: calc(var(--range-input-track-height) * .5);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.drifter-range-input::-moz-range-track {
  height: var(--range-input-track-height);
  border-radius: calc(var(--range-input-track-height) * .5);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.drifter-range-input::-ms-track {
  width: 100%;
  height: var(--range-input-track-height);
  border-radius: calc(var(--range-input-track-height) * .5);
  border-color: transparent;
  color: transparent;
  background: transparent;
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.drifter-range-input:hover::-webkit-slider-thumb {
  background-color: var(--accent-color-complement);
}

.drifter-range-input:hover::-moz-range-thumb {
  background-color: var(--accent-color-complement);
}

.drifter-range-input:hover::-ms-thumb {
  background-color: var(--accent-color-complement);
}


.drifter-knob {
  /* public properties */
  --knob-value: 0;
  --knob-min: 0;
  --knob-max: 100;
  /* protected properties */
  --knob-track-length: calc(var(--range-input-track-height) * 8);
  --knob-dial-size: calc(var(--range-input-track-height) * 4);
  --knob-mark-size: calc(var(--knob-dial-size) * .125);

  position: relative;
  left: calc((
    (var(--knob-track-length) * .5) -
    (var(--knob-dial-size) * .5)
  ) * -1);
  width: var(--knob-track-length);
  height: -webkit-max-content;
  height: max-content;
  padding-top: calc(var(--knob-dial-size) + var(--range-input-track-height));
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: 50% calc(var(--knob-dial-size) * .5);
          transform-origin: 50% calc(var(--knob-dial-size) * .5);
}

.drifter-knob:before,
.drifter-knob:after {
  content: '';
  position: absolute;
  left: calc(50% - (var(--knob-dial-size) * .5));
  display: block;
  width: var(--knob-dial-size);
  cursor: ns-resize;
}

/* knob dial */
.drifter-knob:before {
  top: 0;
  height: var(--knob-dial-size);
  border-radius: 50%;
  border: calc(var(--knob-mark-size) * .5) solid var(--accent-color);
}

/* knob mark */
.drifter-knob:after {
  top: calc((var(--knob-dial-size) * .5) - (var(--knob-mark-size) * .5));
  height: var(--knob-mark-size);
  border-left: calc(var(--knob-mark-size) * 2) solid var(--accent-color-complement);
  -webkit-transform: rotate(calc(((300deg / var(--knob-max)) * var(--knob-value)) + 30deg));
          transform: rotate(calc(((300deg / var(--knob-max)) * var(--knob-value)) + 30deg));
}

.drifter-knob::-webkit-slider-thumb { opacity: .25; }
.drifter-knob::-moz-range-thumb { opacity: .25; }
.drifter-knob::-ms-thumb  { opacity: .25; }

.drifter-knob::-webkit-slider-runnable-track { opacity: .25; }
.drifter-knob::-moz-range-track { opacity: .25; }
.drifter-knob::-ms-track { opacity: .25; }

.drifter-knob:not(:hover)::-webkit-slider-thumb { visibility: hidden; }
.drifter-knob:not(:hover)::-moz-range-thumb { visibility: hidden; }
.drifter-knob:not(:hover)::-ms-thumb { visibility: hidden; }

.drifter-knob:not(:hover)::-webkit-slider-runnable-track { visibility: hidden; }
.drifter-knob:not(:hover)::-moz-range-track { visibility: hidden; }
.drifter-knob:not(:hover)::-ms-track { visibility: hidden; }


.drifter-label {
  display: block;
  line-height: 2;
  white-space: nowrap;
}

.drifter-has-neumorphism {
  --neumorphism-bg-color: var(--background-color-complement);
  --neumorphism-shadow-color-tint: var(--background-color-tint);
  --neumorphism-shadow-color-shade: var(--background-color-shade);

  --neumorphism-shadow-offset: .0375rem;
  --neumorphism-shadow-blur: calc(var(--neumorphism-shadow-offset) * 10);
  --neumorphism-shadow-spread: calc(var(--neumorphism-shadow-offset) * -.5);

  background-color: var(--neumorphism-bg-color);
  box-shadow:
    calc(var(--neumorphism-shadow-offset) * -1)
    calc(var(--neumorphism-shadow-offset) * -1)
    var(--neumorphism-shadow-blur)
    var(--neumorphism-shadow-spread)
    var(--neumorphism-shadow-color-tint),
    var(--neumorphism-shadow-offset)
    var(--neumorphism-shadow-offset)
    var(--neumorphism-shadow-blur)
    var(--neumorphism-shadow-spread)
    var(--neumorphism-shadow-color-shade);
}


.drifter-select,
.drifter-select .drifter-select-option {
  height: var(--select-height);
  outline: none;
  border: 0 none;
  color: var(--accent-color);
}

.drifter-select {
  --select-height: 1.5rem;
  -webkit-padding-start: .5em;
          padding-inline-start: .5em;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
  border-radius: calc(var(--select-height) * .35);
  font: bold 1rem trench, sans-serif;
}

.drifter-select:hover,
.drifter-select:focus {
